@import "../base/mixins";
@import "../base/variables";

.articles {
  .article-wrap {
    @include clearfix;
    border-bottom: 1px solid $light-gray;
    margin: 0 0 1.5em;
    .article-image-wrap {
      margin: 0 0 .5em;
      .article-image {
        overflow: hidden;
        padding-bottom: 56.25%;
        position: relative;
        img {
          bottom: 0;
          left: 0;
          position: absolute;
          right: 0;
          top: 0;
        }
      }
    }
    .article {
      font-size: .875em;
      h2 {
        font-family: $helvetica-bold;
        font-size: 1.25em;
        line-height: 1.125em;
        margin: 0 0 .375em;
      }
      h3 {
        font-family: $helvetica-bold;
        font-size: 1em;
        line-height: 1;
        margin: 0 0 1em;
      }
    }
  }
}


#main #main-inner #content #content-area {
    .article {
        .article-image {
            float: right;
            width: 25%;
            margin-left: 1em;
        }
    }
}




@media all and ( min-width: 768px ) {
  
  .articles {
    .article-wrap {
      .article-image-wrap {
        float: left;
        margin: 0 4% .5em 0;
        width: 21%; 
      }
      .article {
        float: left;
        margin: 0;
        width: 75%;
      }
    }
  }
  
}


@media all and ( min-width: 1200px ) {
  
  .articles {
    .article-wrap {
      padding: 0 0 1em;
      .article-image-wrap {
        margin: 0 2% .5em 0;
        width: 23%; 
      }
    }
  }
  
}