//=====================================================
// General
//=====================================================

// Client Branding Colors
$brand-dark-green: #004600;
$brand-green: #046a38;
$brand-tan: #999966;
$brand-ivory: #ffffcc;
$brand-blue: #00aeef;

// General Colors
$black: #333;
$very-dark-gray: #666;
$dark-gray: #939393;
$medium-gray: #bfbfbf;
$light-gray: #dadada;
$very-light-gray: #f0f0f0;
$light-red: #FBE3E4;
$dark-red: #91090e;
$light-green: #E6EFC2;
$light-yellow: #FFF6BF;
$very-light-yellow: #ffffee;
$yellow: #fee429;
$brown: #5c452e;
$dark-brown: #483d38;
$blue: #00aeef;
$light-orange: #fce;
$white: #FFFFFF;
$beige: #F0EEE2;
$orange: #EB8200;
$canton-blue: #00AEEF;
$canton-light-blue: #B2E6FA;


// Transitions & Animations
$transitions: all 0.2s ease;

//=====================================================
// Components
//=====================================================

//  Fonts

$helvetica-roman: 'HelveticaNeueW01-55Roma';
$helvetica-medium: 'HelveticaNeueW01-65Medi';
$helvetica-bold: 'HelveticaNeueW01-75Bold';
$helvetica-heavy: 'HelveticaNeueW01-85Heav';
$helvetica-condensed-roman: 'HelveticaNeueW01-57Cn';
$helvetica-condensed-heavy: 'HelveticaNeueW01-87HvCn_692734';


$base-font-color: $brown;
$base-accent-color: $brand-green;
$base-link-color: $brand-green;
$hover-link-color: $brand-dark-green;
$h16-color-text: $black;
$h16-color-text-hover: $dark-gray;

// Sizes
$base-font-size: 1em;
$base-line-height: 1.5em;

// Typography
$sans-serif: $helvetica-roman, helvetica, arial, sans-serif;
$serif: georgia, serif;
$base-font-family: $sans-serif;

// Buttons
$button-color-back: $brand-green;
$button-color-back-hover: $brand-dark-green;
$button-color-back-active: $brand-dark-green;
$button-color-text: $white;
$button-border: none;
$button-border-radius: 6px;

// Borders
$base-border-radius: em(3);
$base-border-color: $light-gray;

// Tables
$table-color-border: $light-gray;

// Flash Colors
$error-color: $light-red;
$notice-color: $light-yellow;
$success-color: $light-green;

//=====================================================
// Regions
//=====================================================

// General
$max-width: 1200px;
$max-full-width: 100%;

// Body
$body-color-back: $white;
$body-color-text: $base-font-color;

// Utility Bar
$utility-color-back: $brand-dark-green;
$utility-color-text: $white;
$utility-color-text-hover: $white;

// Header Colors
$header-color-back: $brand-green;
$header-color-text: $white;
$header-color-text-hover: $white;

// Nav
$nav-color-back: transparent;
$nav-color-back-hover: $brand-dark-green;
$nav-color-text: white;
$nav-color-text-hover: white;

// Main
$main-color-back: $white;
$main-inner-color-back: $white;

// Superfooter Colors
$color-back-superfooter: transparent;
$color-text-superfooter: white;
$color-text-superfooter-hover: $very-light-gray;

// Footer Colors
$footer-color-back: transparent;
$footer-color-text: $white;

